@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

p{
  margin: 20px;
}
.App{
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  position: relative;
  font-family: 'Roboto', sans-serif;
}
.App::before{
  content: "";
  position: absolute;
  left: 0%;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  opacity: .6;
  background: url(/random-lottery-numbers-generator/static/media/background.be8120df.jpg) no-repeat;
  background-size: cover;
}
.title{
  position: absolute;
  top: 15%;
  left: 0;
  width: 90%;
  text-align: right;
  padding: 20px 10px 5px 0;
  background-color: rgba(0, 255, 255, 0.3);
  color: rgb(11, 32, 56);
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  border-top: 2px solid rgba(0, 0, 0, 0.4);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
  border-radius: 0px 5px 5px 0px;
}
.title-2{
  position: absolute;
  top: 25%;
  left: 0;
  width: 80%;
  text-align: right;
  padding: 20px 10px 5px 0;
  background-color: rgba(0, 255, 255, 0.3);
  color: rgb(11, 32, 56);
  border-right: 2px solid rgba(0, 0, 0, 0.4);
  border-top: 2px solid rgba(0, 0, 0, 0.4);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
  border-radius: 0px 5px 5px 0px;
}

.LuckyNumbers{
  width: 100vw;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.sixnumbers,.euronumbers{
  width: 30%;
  padding: 30px;
  /* height: 50%; */
  /* border: 1px solid black; */
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 25px;
  margin-top: 20px;
  border: 4mm ridge rgba(95, 158, 160, 0.8);
  background-color: rgba(255, 255, 255, 0.5);
}
.sixnumbers div:nth-child(7){
  background-color: rgba(95, 158, 160, 0.8);
}
.euronumbers div:nth-child(7),
.euronumbers div:nth-child(6){
  background-color: rgba(95, 158, 160, 0.8);
}
.number{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  width: 50px;
  height: 50px;
  border: 2px solid cadetblue;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.8);
}
.buttons{
  width: 30%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 40px;
}

.buttons button:nth-child(1){
  color: cadetblue;
  width: 29%;
  border: 2px solid cadetblue;
  background-color: rgba(255, 255, 255, 0.8);
  font-size: medium;
  font-weight: 700;
}
.buttons button:nth-child(2){
  background-color: rgba(95, 158, 160, 0.8);
  color: white;
  width: 69%;
  border: 2px solid cadetblue;
  font-size: medium;
  font-weight: 700;
}
button:active{
  outline: 2px solid lightseagreen;
}

.choicebtn{
  width: 220px;
  height: 50px;
  margin: 20px;
}
.start button:nth-child(1){
  background: url(/random-lottery-numbers-generator/static/media/logo-lotto6aus49.8250c2e9.svg) no-repeat;
  background-size: cover;
  background-position: center;
  background-origin: content-box;
  padding: 10px;
  background-color: rgba(0, 255, 255, 0.6);
}
.start button:nth-child(2){
  background: url(/random-lottery-numbers-generator/static/media/logo-eurojackpot.d04140be.svg) no-repeat;
  background-size: cover;
  background-size: contain;
  background-origin: content-box;
  padding: 10px;
  background-position: center;
  background-color: rgba(0, 255, 255, 0.6);
}

button{
  cursor: pointer;
}
